import React, { useEffect, useRef } from "react";
import { createGitgraph } from "@gitgraph/js";

const ExperienceGraph = ({ experience }) => {
    const graphContainer = useRef(null);
  
    useEffect(() => {
      if (!graphContainer.current) {
        return;
      }
  
      const graph = createGitgraph(graphContainer.current);
      const master = graph.branch("master");
  
      // Reverse the experience array to match the order of the Card elements
      [...experience].reverse().forEach((exp, i) => {
        master
          .commit({
            subject: `${exp.company} - ${exp.position}`,
            body: `From: ${exp.from}\nTo: ${exp.to}\nYear: ${exp.year}`,
          })
          .tag({ name: `Job ${experience.length - i}`, style: { color: "white" } }); // Adjust the tag's name accordingly
      });
    }, [experience]);
  
    return <div ref={graphContainer} />;
  };
  
  export default ExperienceGraph;