import React from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import ExperienceGraph from './ExperienceGraph'; 

const Resume = ({ personalInfo, education, experience }) => (
  <Container>
    <Row>
      <Col>
        <h2>{personalInfo.name}</h2>
        <p>Email: {personalInfo.email}</p>
        <p>Phone: {personalInfo.phone}</p>
      </Col>
    </Row>

    <Row>
      <Col>
        <h3>Education</h3>
        {education.map((edu, index) => (
          <Card key={index}>
            <Card.Body>
              <Card.Title>{edu.institution}</Card.Title>
              <Card.Text>{edu.degree}</Card.Text>
              <Card.Text>{edu.year}</Card.Text>
            </Card.Body>
          </Card>
        ))}
      </Col>
    </Row>

    <Row>
      <Col sm={6}>
        <h3>Experience</h3>
        {experience.map((exp, index) => (
          <Card key={index}>
            <Card.Body>
              <Card.Title>{exp.company}</Card.Title>
              <Card.Text>{exp.position}</Card.Text>
              <Card.Text>From: {exp.from}</Card.Text>
              <Card.Text>To: {exp.to}</Card.Text>
              <Card.Text>Year: {exp.year}</Card.Text>
            </Card.Body>
          </Card>
        ))}
      </Col>
      
      <Col sm={6}>
        <h3>Experience Timeline</h3>
        <ExperienceGraph experience={experience} />
      </Col>
    </Row>
  </Container>
);

export default Resume;